.sticky{position: sticky;}
.abs{
    position: absolute;
    &.fill{
        top: 0px;
        left: 0px;
        right: 0px;
        bottom: 0px;
    }
    &.abc{
        top: 50%;left: 50%;transform: translate(-50%, -50%);
    }
}
.abcb:before{
    position: absolute;
    top: 50%;left: 50%;transform: translate(-50%, -50%);
}
.nope{pointer-events: none;}
.rel{position: relative;}
.fixed{position: fixed}

